import React from 'react';
import ReactDOM from 'react-dom';

const e = React.createElement;

class TodoItem extends React.Component {
  constructor(props) {
    super(props);
    this.state = { text: "Default", done: false };
  }

  render() {
    if (this.state.done) {
      return 'You finished this.';
    }

    return (
      e('container', {}, 
      [
        this.state.text,
        e(
            'input',
            {type:'checkbox', onClick: () => this.setState({ done: true }) },
            )
      ]
    ));
  }
}

const domContainer = document.querySelector('#root');
ReactDOM.render(e(TodoItem), domContainer);